<template>
   <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">

          <div class="modal-header">
            <slot name="header">
              회원정보수정
            </slot>
          </div>
          <div class="modal-body">
            <slot name="body">
             비밀번호확인
            </slot>
          </div>

          <div class="modal-footer">
            <slot name="footer">
               <input type="password" id="password" v-model="password">
               <br>
              <el-button type="primary" size="mini" @click="check_password">
                확인
              </el-button>
              <el-button type="danger" size="mini" @click="$emit('close')">
                취소
              </el-button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
    data: function () {
  return {
      password:''
      ,body:this.$route.query
  }
},
methods:{
    check_password: function(){
        this.$http.post('/users/check_pass', {password: this.password,id:localStorage.getItem("id") }) 
    .then((res) => {
      if (res.data.success == true) {
          this.body={
              password:this.password
          }
       	this.$router.push({path:'/create',query:this.body});
      }
      if (res.data.success == false) {
        alert(res.data.message);
      }
    })
    .catch(function (error) {
      alert(error)
    })
    }
}
}
</script>
<style scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 15px;
  border:3px solid #33CC33;;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
  font-weight: bold;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>